import React, { useState } from 'react'
//
import Loader from './Loader'
import {ReactComponent as Accepted}  from '~IMAGE/check-accept.svg'
import {ReactComponent as Close}  from '~IMAGE/close.svg'
//
import './mainScreen.sass'
import Header from './header/Header'
//
const MainScreen = () => {
//
    const [withdrawMethod, setWithdrawMethod] = useState("Payeer wallet") // For image set
    // Header
    const [blockedBalance, setBlockedBalance] = useState('0 $') 
    const [activeBalance, setActiveBalance] = useState('0 $')
    const [userName, setUserName] = useState('-')

    const [userWalletAddressBk, setUserWalletAddressBk] = useState('')
    const [userWalletAddress, setUserWalletAddress] = useState('')
    const [replenishBalance, setReplenishBalance] = useState('-')

    const [commentName, setCommentName] = useState('Comment to payment:')
    const [commentForPayment, setCommentForPayment] = useState()
    // Loader or else
    const [paymentStatus, setPaymentStatus] = useState('Waiting for payment')
    const [paymentType, setPaymentType] = useState('Payment of Commission')
    const [сurrency, setCurrency] = useState('USD')
    const [userCurrency, setUserCurrency] = useState()

    const [rotateIcon, setRotateIcon] = useState(0)
    // Danger text
    const [isCloseDangerText, setIsCloseDangerText] = useState(true)
    const [dangerTextOne, setDangerTextOne] = useState('Closing this web page or not sending the exact amount of Payeer will lead to a failing transaction')
    const [dangerTextTwo, setDangerTextTwo] = useState('The withdrawal is made to the')
    //
    console.log('userCurrency', сurrency)
    return (
        <>
            <div className='mainScreen'>

                <Header
                    blockedBalance={blockedBalance}
                    activeBalance={activeBalance}
                    userName={userName}
                />

                <div style={{ height: `${!isCloseDangerText ? '380px' : ''}` }} className='mainContent'>
                    <div className='mainContent--item mainContent--text-layer'>
                        <h1 className=''>{paymentType}</h1>
                        {isCloseDangerText ?
                            <div>
                                <p className='text-danger'> {dangerTextOne} </p>
                                <p className='text-danger '> {dangerTextTwo} {withdrawMethod} {userWalletAddress}</p>
                            </div>
                            : ''
                        }
                        <div  style={{ transform: `rotate(${rotateIcon}deg)` }}>
                            <img
                                style={{ background: 'transparent', height: '42px', width: '200px' }}
                                src={ require(`./../../../images/payer.png`).default} 
                            />
                        </div>
                    </div>
                    
                    
                    <div className='mainContent--item justifySpaceAround' style={{ width: '100%'}}>
                        
                        <div className='df'>
                            <div>
                                <p>Amount of Payeer:</p>
                            </div>
                            <div style={{display: 'flex'}}>
                                <p className='tLeft'>{replenishBalance.toUpperCase() }</p>
                                <p className='tLeft' style={{padding: '0'}}>{сurrency.toString() == 'true' ? userCurrency : сurrency}</p>
                            </div>
                        </div>

                        <div className='df'>
                            <div>
                                <p>Payeer wallet address:</p>
                            </div>
                            <div>
                                <p className='tLeft'>{userWalletAddressBk}</p>
                            </div>
                        </div>

                        <div className='df'>
                            <div>
                                <p>{commentName}</p>
                            </div>
                            <div>
                                <p className='tLeft'>{commentForPayment}</p>
                            </div>
                        </div>

                        <div className='df'>
                            <div>
                                <p>Status:</p>
                            </div>
                            <div>
                                <p className='tLeft'>{paymentStatus}</p>
                            </div>
                        </div>

                        <div className='statusIcon'>
                            {
                            paymentStatus === 'Waiting for payment' 
                                ? <Loader />
                                : paymentStatus === 'Accepted'
                                    ? <Accepted />
                                    : <Close /> 
                            }
                        </div>
                    </div>
                </div>
            </div>
            

           
            <div className='dfDc'>
                <div style={{width: '100%'}}>
                    <form>
                        <div style={{ alignItems: 'flex-start'}} className='df df-column-mobile'>

                            <div style={{width: '100%', height: '850px', paddingBottom: '20px', alignItems: 'center',  justifyContent: 'space-around', display: 'flex', flexDirection: 'column'}}>
                                {/* Header */}
                                <div className='input-item'>
                                    <input 
                                        id="blocked" 
                                        value={blockedBalance}
                                        onChange={e => setBlockedBalance( e.target.value )} 
                                    />
                                    <label for="blocked">Заблокированный баланс</label>
                                </div>

                                <div className='input-item'>
                                    <input 
                                        id="blocked" 
                                        value={activeBalance}
                                        onChange={e => setActiveBalance( e.target.value )} 
                                    />
                                    <label for="active">Активный баланс</label>
                                </div>

                                <div className='input-item'>
                                    <input 
                                        id="blocked" 
                                        value={userName}
                                        onChange={e => setUserName( e.target.value )} 
                                    />
                                    <label for="active">Имя пользователя</label>
                                </div>

                                {/* Header end */}

                                <div className='input-item'>
                                    <input 
                                        id="sum" 
                                        onChange={e => setReplenishBalance( e.target.value ) } 
                                        value={replenishBalance}
                                    />
                                    <label for="sum">Сумма пополнения</label>
                                    <select style={{ width: '70%' }}  id="status" onChange={(e) => setCurrency(e.target.value)}>
                                        <option value="USD">USD</option>
                                        <option value="RUB">RUB</option>
                                        <option value={true}>Свой выбор</option>
                                    </select>
                                    {
                                        сurrency.toString() == 'true' && 
                                            <input 
                                                style={{width: '100px'}}
                                                id="sum" 
                                                onChange={e => setUserCurrency( e.target.value ) } 
                                                value={userCurrency}
                                            />
                                    }
                                   
                                </div>
                                

                                <div className='input-item' style={{ justifyContent: 'center'}}>
                                    <select style={{ width: '70%' }}  id="status" onChange={(e) => setPaymentType(e.target.value)}>
                                        <option value="Payment of Commission">Payment of Commission</option>
                                        <option value="Deposit payment">Deposit payment</option>
                                        <option value="Verification Payment">Verification Payment</option>
                                        <option value="Private Services">Private Services</option>
                                    </select>
                                    <input
                                        id="scare" 
                                        value={paymentType} 
                                        onChange={e => setPaymentType( e.target.value )} 
                                    />
                                    <label for="comment">Тип оплаты</label>
                                </div>

                                <div className='input-item'>
                                    <input 
                                        id="scare" 
                                        value={userWalletAddress} 
                                        onChange={e => setUserWalletAddress( e.target.value )} 
                                    />
                                    <label for="scare">Номер кошелька клиента</label>
                                </div>

                                <div className='input-item' style={{ justifyContent: 'center'}}>
                                    <input
                                        style={{ overflow: 'auto' }}
                                        id="scare" 
                                        value={dangerTextOne} 
                                        onChange={e => setDangerTextOne( e.target.value )} 
                                    />
                                    <label for="comment">Красный текст 1</label>

                                    <input 
                                        style={{position: 'absolute', top: '-7px', right: '10px'}}
                                        type="checkbox"
                                        onClick={() => setIsCloseDangerText(!isCloseDangerText)}
                                    ></input>
                                    <span style={{position: 'absolute', top: '7px', right: '40px'}} >Не показывать текст?</span>

                                </div>

                            </div>


                            <div style={{width: '100%', alignItems: 'center', borderLeft: '1px solid #fff', height: '850px', paddingBottom: '20px', justifyContent: 'space-around', display: 'flex', flexDirection: 'column'}}>

                                <div className='input-item mobile-height' >
                                    <label for="scare">Система вывода</label>
                                    <div className='button-set'>
                                        <button onClick={() => setWithdrawMethod('Payeer wallet')}>Payeer</button>
                                        <button onClick={() => setWithdrawMethod('MIR')}>MIR</button>
                                        <button onClick={() => setWithdrawMethod('Mastercard')}>Mastercard</button>
                                        <button onClick={() => setWithdrawMethod('Visa')}>Visa</button>
                                    </div>
                                    <input 
                                        id="scare" 
                                        value={withdrawMethod} 
                                        onChange={e => setWithdrawMethod( e.target.value )} 
                                    />
                                </div>

                                <div className='input-item'>
                                    <input 
                                        id="scare" 
                                        value={userWalletAddressBk } 
                                        onChange={e => setUserWalletAddressBk( e.target.value )} 
                                    />
                                    <label for="scare">Номер кошелька БК</label>
                                </div>

                                <div className='input-item'>
                                        <select className='select' onChange={(e) => setCommentName(e.target.value)}>
                                            <option value="Comment to payment:">Comment to payment:</option>
                                            <option value="Personal ID:">Personal ID:</option>
                                        </select>
                                        <input 
                                            id="scare" 
                                            value={commentName} 
                                            onChange={(e) => setCommentName(e.target.value)}
                                        />
                                    <label for="comment_text">Название поля комментария</label>
                                </div>

                                <div className='input-item'>
                                    <input 
                                        id="comment" 
                                        value={commentForPayment}
                                        onChange={(e) => setCommentForPayment(e.target.value)}
                                    />
                                    <label for="comment">Комментарий к платежу</label>
                                </div>

                                <div className='input-item' style={{ justifyContent: 'center'}}>
                                    <select id="status" onChange={(e) => setPaymentStatus(e.target.value)}>
                                        <option value="Waiting for payment">Waiting for payment</option>
                                        <option value="Accepted">Accepted</option>
                                        <option value="Declined">Declined</option>
                                    </select>
                                    <label for="comment">Статус платежа</label>
                                </div>

                                <div className='input-item'>
                                    <input 
                                        id="comment" 
                                        value={rotateIcon}
                                        onChange={(e) => setRotateIcon(+e.target.value)}
                                    />
                                    <label for="comment">Повернуть иконку</label>
                                </div>

                                <div className='input-item' style={{ justifyContent: 'center'}}>
                                    <input
                                        id="scare" 
                                        value={dangerTextTwo} 
                                        onChange={e => setDangerTextTwo( e.target.value )} 
                                    />
                                    <label for="comment">Красный текст 2</label>
                                </div>

                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </>
    )
}



export default MainScreen